import React from 'react';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import List from '@material-ui/core/List';
import Link from '@material-ui/core/Link';
import { Link as RouterLink } from 'react-router-dom'
import MailIcon from '@material-ui/icons/Mail';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import Aux from '../../hoc/Aux'
import { useRouteMatch } from 'react-router-dom'

function Drawer (props) {
  let match = useRouteMatch();
  return (
    <Aux>
      <div className={props.classes.toolbar} />
      <Divider />
      <List component="nav" className={props.classes.drawerBackground}>
        <List>
          <Link component={RouterLink} to={`${match.url}/dashboard`}>
            <ListItem button key="dashboard" style={{ color: '#ffff'}}>
              <ListItemIcon>
                <InboxIcon style={{ color: '#ffff' }}/>
              </ListItemIcon>
              <ListItemText primary="Dashboard"/>
            </ListItem>
          </Link>
          <Link to={`${match.url}/events`} component={RouterLink} >
            <ListItem button key="events" style={{ color: '#ffff'}}>
              <ListItemIcon>
                <MailIcon style={{ color: '#ffff' }}/>
              </ListItemIcon>
              <ListItemText primary="Events"/>
            </ListItem>
          </Link>
          <Link to={`${match.url}/students`} component={RouterLink} >
            <ListItem button key="students" style={{ color: '#ffff'}}>
              <ListItemIcon>
                <MailIcon style={{ color: '#ffff' }}/>
              </ListItemIcon>
              <ListItemText primary="Students"/>
            </ListItem>
          </Link>
        </List>
        <Divider />
        <List>
          {['All mail', 'Trash', 'Spam'].map((text, index) => (
            <ListItem button key={text} style={{ color: '#ffff'}}>
              <ListItemIcon style={{ color: '#ffff' }}>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
      </List>
    </Aux>
  );
}

export default Drawer
