import React, { Component } from 'react'
import { connect } from 'react-redux'
import ReactModal from 'react-modal'
import StudentList from '../../../components/UI/Students/StudentList'
import AddStudent from '../../../components/UI/Students/AddStudent'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import { withStyles } from '@material-ui/core/styles';



function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  canvasImage:{
    textAlign:'right'
  },
  canvasWrapper:{
    margin:'0 130px',
    backgroundColor:'#E1F1FF',
    borderRadius:'10px',
    padding:'0 120px'
  },
  welcomeHead:{
    color:'#43425D',
    marginBottom:'0',
  },
  welcomeText:{
    color:'#43425D',
    marginTop:'0',
    fontSize:'12px'
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
   paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    maxWidth: "500px"
  },
});






class Students extends Component 
{

  constructor (props) {
    super(props)
    this.state = {
      open: false,
    };

    this.handleOpenModal = this.handleOpenModal.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);

  }

  

  handleOpenModal () {
    this.setState({ open: true });
  }
  
  handleCloseModal () {
    this.setState({ open: false });
  }

  render () {

    const { classes } = this.props
   
    this.classes = classes
    return (
    	<div>
      <Grid container spacing={3}>
        
        <Grid item xs={6}>
          <h3>Student List</h3>
        </Grid>
        <Grid item xs={3}>
          <Button variant="contained" color="primary">
        Import Student
      </Button>
        </Grid>
         <Grid item xs={3}>
          <Button onClick={this.handleOpenModal} variant="contained" color="primary">
        Register new student
      </Button>
        </Grid>
        
      </Grid>
      
    		
        
      		<StudentList data={this.props.studentLists}/>
        <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={this.state.open}
        onClose={this.handleCloseModal}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={this.state.open}>
          <div className={classes.paper}>
            <AddStudent/>
          </div>
        </Fade>
      </Modal>

        </div>
    )
  }
}

function mapStateToProps(state) {
	//console.log(state.tutorReducer.students.students);
   return {
    studentLists: state.tutorReducer.students.students
   }
}

export default connect(
  mapStateToProps,null
)(withStyles(styles)(Students));
