import React, { Component } from 'react';
import { Link as RouterLink } from 'react-router-dom'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { updateStudentData, getStudentData } from '../../../redux/store/actions/tutor.actions'
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator'
import { withStyles } from '@material-ui/core/styles'

import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';

import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography'



const styles = theme => ({
  formBackground: {
    backgroundColor: '#fff'
  },
  'logo': {
    'display' : 'block',
    'border': '2px solid red',
    'borderRadius': '50%',
    'height': '100px',
    'width': '100px',
    'marginLeft': 'auto',
    'marginRight': 'auto'
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(1),
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(2)
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
});


class EditStudent extends Component {
  constructor (props) {
    super(props)
    this.handleLogin = this.handleLogin.bind(this)
    this.state = ({ 
      student : {
        first_name: '',
        last_name: '',
        password: '',
        password_confirmation: '',
        email: '',
        mobile_number: '',
        username : '',
      }
    })
    
  }

  componentDidMount = async() => {

     await this.props.getStudentData(this.props.uuid)
     //this.setState({'student':this.props.edit_student})
     const { student } = this.state
     student['first_name']=this.props.edit_student.first_name
     student['last_name']=this.props.edit_student.last_name
     student['email']=this.props.edit_student.user.email
     student['mobile_number']=this.props.edit_student.user.mobile_number
     student['username']=this.props.edit_student.user.username
     this.setState({ student })
     
  }

  handleLogin = async (e) => {
    e.preventDefault();
    console.log(this.state)
    await this.props.updateStudentData(this.props.id,this.state)

    console.log()

  };

  handleChange = (e) => {
    if (this.props.authenticationError !== '')
      this.props.resetAuthError()
    const { student } = this.state
      student[e.target.name] = e.target.value
    
    this.setState({ student })
  }

  render () {
    const { classes } = this.props
    const { student } = this.state

    let authError = this.props.authenticationError !== '' 
      ? <Grid container>
          <Box color="error.main">{this.props.authenticationError}</Box> 
        </Grid>: ''

    return (
      <React.Fragment>
        <Grid item xs={12}>
                    <Typography component="div">
            <Box textAlign="center" m={1}>
              Register New Student -Class 6 A
            </Box>
          </Typography>
          <ValidatorForm
            onSubmit={this.handleLogin}
            onError={errors => console.log(errors)}
            className={classes.form}
            mx="auto"
          >
          <Grid container spacing={3}>
        <Grid item xs={6}>
          <TextValidator
              margin="normal"
              fullWidth
              label="First Name"
              name="first_name"
              value={student.first_name}
              onChange={this.handleChange}
              validators={['required']}
              errorMessages={['* Please provide first name']}
            />
        </Grid>
        <Grid item xs={6}>
          <TextValidator
              margin="normal"
              fullWidth
              label="Last Name"
              name="last_name"
              value={student.last_name}
              onChange={this.handleChange}
              validators={['required']}
              errorMessages={['* Please provide last name']}
            />
        </Grid>
        <Grid item xs={6}>
          <TextValidator
              margin="normal"
              fullWidth
              label="Email"
              onChange={this.handleChange}
              name="email"
              validators={['required']}
              errorMessages={['* Please provide email']}
              value={student.email}
            />
        </Grid>
        <Grid item xs={6}>
          <TextValidator
              margin="normal"
              fullWidth
              label="Mobile"
              onChange={this.handleChange}
              name="mobile_number"
              validators={['required']}
              errorMessages={['* Please provide mobile_number']}
              value={student.mobile_number}
            />
        </Grid>
        <Grid item xs={6}>
          <TextValidator
              margin="normal"
              fullWidth
              label="Username"
              onChange={this.handleChange}
              name="username"
              validators={['required']}
              errorMessages={['* Please provide username']}
              value={student.username}
            />
        </Grid>
        <Grid item xs={6}>
          <TextValidator
              margin="normal"
              fullWidth
              label="Password"
              type="password"
              onChange={this.handleChange}
              name="password"
              validators={[]}
              errorMessages={['* Please provide password']}
              value={student.password}
            />
        </Grid>
        <Grid item xs={6}>
          <TextValidator
              margin="normal"
              fullWidth
              label="Confirm Password"
              onChange={this.handleChange}
              name="password_confirmation"
              validators={[]}
              errorMessages={['* Please provide password_confirmation']}
              value={student.password_confirmation}
            />
        </Grid>
      </Grid>
            
            
            
            
            
            
            
            
            
            { authError }
            <Button
              type="submit"
              fullWidth
              variant="contained"
              disabled={this.props.isLoading}
              color="primary"
              className={classes.submit}
            >
              Sign in            
            </Button>
          </ValidatorForm>
        </Grid>
      </React.Fragment>
    )
  }
}

const mapStateToProps = state => {
  const { isLoading, authenticationError, isAuthenticated } = state.authReducer
  const {edit_student} = state.tutorReducer
  return {
    isAuthenticated,
    isLoading,
    authenticationError,
    edit_student
  }
}

const mapDispatchToProps = dispatch => {
  return {
    dispatch,
    ...bindActionCreators({ 
      getStudentData,
      updateStudentData, 
    }, dispatch)
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(EditStudent));
