import { makeStyles } from '@material-ui/core/styles';
import {withRouter} from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Switch from '@material-ui/core/Switch';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';

import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';

import EditStudent from '../../../components/UI/Students/EditStudent'

const useStyles = makeStyles((theme)=>({
  root: {
    minWidth: 275,
     flexGrow: 1,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  studentCount:{
    float:'right',
  },
  subjectList:{
    textAlign:'center',
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,
    margin:'10px 5px'
  },
  paper1:{
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    maxWidth: "500px"
  },
  avatar: {
    padding: theme.spacing(2),
    boxShadow:'none'
  },
  switch: {
    fontSize:'10px',
  },
  first_name: {
    fontWeight:'bold',
    fontSize:'17px',
    color:'#505050'
  },
  text: {
    fontSize:'14px'
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
}));


const StudentCard = (props) =>{

   const [open, setOpen] = React.useState(false);

  const handleOpenModal = () => {
    setOpen(true);
  };

  const handleCloseModal = () => {
    setOpen(false);
  };


  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };


  const [state, setState] = React.useState({
    checkedA: true,
    checkedB: true,
  });
   const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  console.log(props);
  const classes = useStyles();
  const bull = <span className={classes.bullet}></span>;

  return (
    <div>
    
      <Paper className={classes.paper}>
      <Grid container  spacing={0}>
      <Grid item xs={3}>
          <Paper className={classes.avatar}><Avatar alt="Remy Sharp" src="/static/images/avatar/1.jpg" /></Paper>
      </Grid>
      <Grid item xs={8}>
           <span className={classes.first_name}>{props.first_name} {props.last_name} </span>
           <div>
           <span className={classes.text}>{props.user.username}</span>
           <span className={classes.text} style={{float:'right'}} className={classes.text}>Roll No:1</span>
           </div>
          <Switch
          checked={state.checkedA}
          onChange={handleChange}
          name="checkedA"
          color="primary"
          inputProps={{ 'aria-label': 'checkbox with default color' }}
         /><span className={classes.switch}>Eduwall</span>
          <Switch
            checked={state.checkedB}
            onChange={handleChange}
            name="checkedB"
            color="primary"
            inputProps={{ 'aria-label': 'checkbox with default color' }}
          /><span className={classes.switch}>Active</span>
      </Grid>
      <Grid item xs={1}>
        <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
        <MoreVertIcon />
        </Button>
        <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          <MenuItem onClick={handleOpenModal}>Edit</MenuItem>
          <MenuItem onClick={handleClose}>Delete</MenuItem>
        </Menu>
      </Grid>
      </Grid>
      </Paper>

      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleCloseModal}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper1}>
            <EditStudent id={props.user.id} uuid={props.uuid}/>
          </div>
        </Fade>
      </Modal>


       
    </div>

  );
}
export default withRouter(StudentCard);