import axios from 'axios'
import {
  BASE_URL,
  TUTOR_API_URL
} from '../config/config'
import JwtService from './jwt.service'

const ApiService = {
  init () {
    axios.defaults.baseURL = BASE_URL
  },
  setHeader () {
    axios.defaults.headers.common[
      "Authorization"
    ] = ` Bearer ${JwtService.getAccessToken()}`;
    axios.defaults.headers.common['Accept'] = 'application/json'
  },
  query(resource, params) {
    return axios.get(resource, params).catch(error => {
      throw new Error(`[EPL] ApiService ${error}`);
    });
  },

  get(resource) {
    return axios.get(`${resource}`).catch(error => {
      throw new Error(`[EPL] ApiService ${error}`);
    });
  },

  post(resource, params) {
    return axios.post(`${resource}`, params);
  },

  update(resource, params) {
    return axios.put(`${resource}`, params);
  },

  delete(resource) {
    return axios.delete(resource).catch(error => {
      throw new Error(`[EPL] ApiService ${error}`);
    });
  }
}

export default ApiService

export const authService = {
  login (params) {
    return ApiService.post(`${BASE_URL}/login`, params);
  },
  logout () {
    return ApiService.get(`${BASE_URL}/logout`);
  }
}

export const tutorDashboardService = {
  dashboard () {
    return ApiService.get(`${BASE_URL}${TUTOR_API_URL}`)
  }
}

export const insertStudentService = {
  save_student (params) {
    return ApiService.post(`${BASE_URL}${TUTOR_API_URL}/student/create`, params)
  }
}

export const getStudentService = {
  get_student (data) {
    return ApiService.get(`${BASE_URL}${TUTOR_API_URL}/student/edit/${data}`)
  }
}

export const updateStudentService = {
  update_student (uuid,params) {
    return ApiService.update(`${BASE_URL}${TUTOR_API_URL}/student/update/${uuid}`,params)
  }
}

export const deleteStudentService = {
  delete_student (id) {
    return ApiService.delete(`${BASE_URL}${TUTOR_API_URL}/student/delete/${id}`)
  }
}
