import { 
  AUTH_REQUEST,
  SET_AUTH_ERROR,
  AUTH_FAILED,
  AUTH_SUCCESS,
  RESET_AUTH_ERROR,
  PURGE_AUTH
} from '../action-types/auth.actions.type'

import {
  CLIENT_ID,
  CLIENT_SECRET
} from '../../../config/config'

import { authService } from '../../../services/api.service'
import jwtService from '../../../services/jwt.service'
import ApiService from '../../../services/api.service'

export const makeAuthRequest = (data)  => {
  return (dispatch) => {
    let { credentials } = data
    let oauth2Param = {
      'grant_type': 'password',
      'client_id': CLIENT_ID,
      'client_secret': CLIENT_SECRET,
      'username': credentials.username,
      'password': credentials.password,
      'scope': '*'
    }

    dispatch(authRequest());
    return authService.login(oauth2Param)
      .then(({ data }) => {
        let { access_credentials, role } = data

        jwtService.saveAccessToken(access_credentials.access_token);
        jwtService.saveRefreshToken(access_credentials.refresh_token);
        jwtService.saveExpirationTime(access_credentials.expires_in);
        jwtService.saveLoginType(role);

        ApiService.setHeader();

        dispatch(authSuccess(role))
      }).catch(({response}) => {
        dispatch(authFailed())
        dispatch(setAuthError(response.data.message))
      })
  }
}

export const appLogout = () => {
  return (dispatch) => {
    authService.logout()
      .then(({ data }) => {
        let { status } = data
        if (status) {
          jwtService.removeAccessToken();
          jwtService.removeRefreshToken();
          jwtService.removeExpirationTime();
          jwtService.removeLoginType();

          dispatch(purgeAuth())
          window.location.href = '/'
        }
      })
  }
}

export const resetAuthError = () => ({
  type:RESET_AUTH_ERROR
})

export const authRequest = () => ({
  type: AUTH_REQUEST,
}) 

export const setAuthError = (message) => ({
	type:SET_AUTH_ERROR,
  payload: message
})

export const authFailed = () => ({
  type: AUTH_FAILED,
})

export const authSuccess = (user) => ({
  type: AUTH_SUCCESS,
  payload: user
})

export const purgeAuth = () => ({
  type: PURGE_AUTH
})
