import {
  FETCH_TUTOR_DASHBOARD_DATA,
  RESET_TUTOR_INITIAL_STATE,
  SAVE_STUDENT_DATA,
  SAVE_EDIT_STUDENT_DATA,
  RESET_STUDENT_INITIAL_STATE,
  UPDATE_STUDENT_DATA,
  DELETE_STUDENT_DATA
} from '../action-types/tutor.actions.type'

import { tutorDashboardService,insertStudentService,getStudentService,updateStudentService,deleteStudentService } from '../../../services/api.service'

export const getTutorDashboardData = () => {
  return (dispatch) => {
    return tutorDashboardService.dashboard()
      .then(({ data }) => {
        dispatch(fetchTutorDashboardData(data))
      })
      .catch(({ response}) => {
        console.log(response)
      })
  }
}


export const fetchTutorDashboardData = (data) => ({
  type: FETCH_TUTOR_DASHBOARD_DATA,
  payload: data
})

export const resetTutorInitialState = () => ({
  type: RESET_TUTOR_INITIAL_STATE
})


export const inserStudentData = (data) => {
  return (dispatch) => {
    return insertStudentService.save_student(data.student)
      .then(({ data }) => {
       dispatch(saveStudentData(data.student))
      })
      .catch(({ response}) => {
        console.log(response)
      })
  }
}

export const saveStudentData = (data) => ({
  type: SAVE_STUDENT_DATA,
  payload: data
})

export const resetStudentInitialState = () => ({
  type: RESET_STUDENT_INITIAL_STATE
})

export const getStudentData = (data) => {
  return (dispatch) => {
    return getStudentService.get_student(data)
      .then(({ data }) => {
       dispatch(saveEditStudentData(data.student))
      })
      .catch(({ response}) => {
        console.log(response)
      })
  }
}

export const saveEditStudentData = (data) => ({
  type: SAVE_EDIT_STUDENT_DATA,
  payload: data
})

export const updateStudentData = (uuid,data) => {
  return (dispatch) => {
    return updateStudentService.update_student(uuid,data.student)
      .then(({ data }) => {
       //dispatch(saveEditStudentData(data.student))
      })
      .catch(({ response}) => {
        console.log(response)
      })
  }
}

export const deleteStudentData = (id) => {
  return (dispatch) => {
    return deleteStudentService.delete_student(id)
      .then(({ data }) => {
       //dispatch(saveEditStudentData(data.student))
      })
      .catch(({ response}) => {
        console.log(response)
      })
  }
}


