import {
  FETCH_TUTOR_DASHBOARD_DATA,
  RESET_TUTOR_INITIAL_STATE,
  SAVE_STUDENT_DATA,
  SAVE_EDIT_STUDENT_DATA
} from '../action-types/tutor.actions.type'

const initialState = {
  userDetails: '',
  tutorSubjects: '',
  tutorAssignedClass: '',
  tutorAssignedClasses: '',
  exams: '',
  students: ''
};

const tutorReducer = (state = initialState, action) => {
  switch(action.type) {
    case FETCH_TUTOR_DASHBOARD_DATA : {
      let { 
        tutor_subjects,
        tutor_assigned_class,
        exams,
        tutor_assigned_classes,
        user_details,
        students
      } = action.payload
      return {
        ...state,
        userDetails: user_details,
        tutorSubjects: tutor_subjects,
        tutorAssignedClass: tutor_assigned_class,
        tutorAssignedClasses: tutor_assigned_classes,
        exams: exams,
        students: students
      }
    }
    case SAVE_STUDENT_DATA : {
      return {
        ...state,
        students: [...state.students, action.payload]
      }
    }
    case RESET_TUTOR_INITIAL_STATE : {
      return {
        ...state,
        userDetails: '',
        tutorSubjects: '',
        tutorAssignedClass: '',
        tutorAssignedClasses: '',
        exams: '',
        students: ''
      }
    }
    case SAVE_EDIT_STUDENT_DATA : {
      return {
        ...state,
        edit_student: action.payload
      }
    }
    default:
      return state
  }
}


export default tutorReducer