const ACCESS_TOKEN = 'access_token'
const REFRESH_TOKEN = 'refresh_token'
const EXPIRATION_TIME = 'expiration_time'
const LOGIN_TYPE = 'login_type'

export const getAccessToken = () => {
  return window.localStorage.getItem(ACCESS_TOKEN)
}

export const saveAccessToken = token => {
  window.localStorage.setItem(ACCESS_TOKEN, token)
}

export const removeAccessToken = () => {
  window.localStorage.removeItem(ACCESS_TOKEN);
}

export const getLoginType = () => {
  return window.localStorage.getItem(LOGIN_TYPE)
}

export const saveLoginType = loginType => {
  window.localStorage.setItem(LOGIN_TYPE, loginType)
}

export const removeLoginType = () => {
  window.localStorage.removeItem(LOGIN_TYPE)
}

export const getRefreshToken = () => {
  return window.localStorage.getItem(REFRESH_TOKEN)
}

export const saveRefreshToken = refresh_token => {
  window.localStorage.setItem(REFRESH_TOKEN, refresh_token)
}

export const removeRefreshToken = () => {
  window.localStorage.removeItem(REFRESH_TOKEN);
}

export const getExpirationTime = () => {
  return window.localStorage.getItem(EXPIRATION_TIME)
}

export const saveExpirationTime = expiration_time => {
  window.localStorage.setItem(EXPIRATION_TIME, expiration_time)
}

export const removeExpirationTime = () => {
  window.localStorage.removeItem(EXPIRATION_TIME);
}

const jwtService = {
  getAccessToken,
  saveAccessToken,
  removeAccessToken,
  getLoginType,
  saveLoginType,
  removeLoginType,
  getRefreshToken,
  saveRefreshToken,
  removeRefreshToken,
  getExpirationTime,
  saveExpirationTime,
  removeExpirationTime
};

export default jwtService
