import React, {Component } from 'react'
import { connect } from 'react-redux'
import Aux from '../src/hoc/Aux'
import GuestHome from '../src/pages/GuestHome'
import TutorHome from '../src/pages/Tutor/Home'
import { BrowserRouter as Router} from 'react-router-dom';
import ProtectedRoute from '../src/hoc/ProtectedRoute'
import PublicRoute from '../src/hoc/PublicRoute'
import ApiService from '../src/services/api.service'

class App extends Component {

  componentDidMount () {
    if (this.props.isAuthenticated)
      ApiService.setHeader()
  }

  render () {
    return (
      <Aux>
        <Router>
          <PublicRoute path="/"
            component={GuestHome}
            isAuthenticated={this.props.isAuthenticated}
            role={this.props.loginType}
          />
          <ProtectedRoute
            path="/tutor"
            component={TutorHome} 
            isAuthenticated={this.props.isAuthenticated} 
          />
        </Router>
      </Aux>
    );
  }
}

const mapStateToProps = state => {
  const { isAuthenticated , loginType } = state.authReducer
  return {
    isAuthenticated,
    loginType
  }
}

export default connect(
  mapStateToProps,
  null
)(App);
