import React, {Component } from 'react'
import { withStyles } from '@material-ui/core/styles';
import { Switch, Route,  BrowserRouter as Router } from 'react-router-dom'
import WithClass from '../hoc/withClass'

import Container from '@material-ui/core/Container';
import ScopedCssBaseline from '@material-ui/core/ScopedCssBaseline';
import Grid from '@material-ui/core/Grid';
import Hidden from '@material-ui/core/Hidden';
import HomeLoginBg from '../assets/images/home-login.png'
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography'

import Login from './Auth/Login'
import ForgotPassword from './Auth/ForgotPassword'
import EduportalLogo from '../assets/images/eduportal.png'

const styles = theme => ({
  root: {
    display: 'flex',
    overflow: 'hidden'
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
  },
  primary: {
    color: '#4D4F5C',
    fontSize: '18px',
    padding: '8px',
    opacity: '50%'
  },
  container: {
    flexGrow: 1,
    padding: 0,
    margin: 0
  },
  'homeBg': {
    width: '100%',
    height: '100%'
  },
  footer: {
    textAlign: 'center'
  }
});

class GuestHome extends Component
{
  constructor(props) {
    super(props)
    this.classes = this.props.classes
  }

  componentDidMount () {
    const { classes } = this.props
    this.classes = classes
  }

  render () {
    return (
      <WithClass classes={this.classes}>
        <Container component="main" className={this.classes.container}>
          <Grid container spacing={5} mt={2}>
            <Hidden only="xs">
              <Grid item 
                md={7}
                lg={7}
                p={0}
              >
                <img src={HomeLoginBg} className={this.classes.homeBg} alt="eduportal-cover"/>
              </Grid>
            </Hidden>
            <Grid item xs={12} sm={12} md={5} lg={5}>
              <Box component="section">
                <Switch>
                  <Route exact path="/" component={Login} />
                  <Route path="/forgot-password" component={ForgotPassword} />
                </Switch>
              </Box>
              <Box component="section">
                <Box component="div" className={this.classes.footer}>
                  <img src={EduportalLogo} alt="eduportal"/>
                  <Typography className={this.classes.primary}>
                    &copy; 2021 Sparkeduportal
                  </Typography>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </Container>
      </WithClass>
    )
  }
}

export default withStyles(styles)(GuestHome)
