import { 
  AUTH_REQUEST,
  SET_AUTH_ERROR,
  AUTH_FAILED,
  AUTH_SUCCESS,
  PURGE_AUTH,
  RESET_AUTH_ERROR
} from '../action-types/auth.actions.type'
import jwtService from '../../../services/jwt.service'


const initialState = {
  isAuthenticated: !!jwtService.getAccessToken(),
  loginType: jwtService.getLoginType(),
  authenticationError: '',
  isLoading: false
};

const authReducer = (state = initialState, action) => {
  switch(action.type) {
    case AUTH_REQUEST : {
      return {
        ...state,
        isLoading: true
      }
    }
    case SET_AUTH_ERROR: {
      return {
        ...state,
        authenticationError: action.payload
      }
    }
    case RESET_AUTH_ERROR: {
      return {
        ...state,
        authenticationError: ''
      }
    }
    case AUTH_FAILED: {
     return {
      ...state,
      isLoading: false
     }
    }
    case AUTH_SUCCESS: {
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        loginType: action.payload
      }
    }
    case PURGE_AUTH: {
      return {
        ...state,
        isAuthenticated: false,
        loginType: ''
      }
    }
    default:
      return state;
  }
}

export default authReducer;
