import React, { Component } from 'react';
import { Link as RouterLink } from 'react-router-dom'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { makeAuthRequest, resetAuthError } from '../../redux/store/actions/auth.actions'
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator'
import { withStyles } from '@material-ui/core/styles'

import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';

import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography'

import AppLogo from '../../components/UI/AppLogo'

const styles = theme => ({
  formBackground: {
    backgroundColor: '#fff'
  },
  'logo': {
    'display' : 'block',
    'border': '2px solid red',
    'borderRadius': '50%',
    'height': '100px',
    'width': '100px',
    'marginLeft': 'auto',
    'marginRight': 'auto'
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(1),
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(2)
  },
  primary: {
    color: '#4D4F5C',
    fontSize: '18px',
    opacity: '50%',
    padding: '8px',
  },
  formSection: {
    marginLeft: theme.spacing(3),
    marginRight: theme.spacing(3)
  },
  rememberSection: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2)
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
});


class Login extends Component {
  constructor (props) {
    super(props)
    this.handleLogin = this.handleLogin.bind(this)
    this.state = ({ 
      credentials : {
        username: '',
        password: ''
      }
    })
  }

  handleLogin = async (e) => {
    e.preventDefault();
    await this.props.makeAuthRequest(this.state)

    console.log(this.props.loginType)

  };

  handleChange = (e) => {
    if (this.props.authenticationError !== '')
      this.props.resetAuthError()

    const { credentials } = this.state
    credentials[e.target.name] = e.target.value
    this.setState({ credentials })
  }

  render () {
    const { classes } = this.props
    const { credentials } = this.state

    let authError = this.props.authenticationError !== '' 
      ? <Grid container>
          <Box color="error.main">{this.props.authenticationError}</Box> 
        </Grid>: ''

    return (
      <React.Fragment>
        <Grid item xs={12} md={12} lg={12}>
          <AppLogo />
          <Typography component="div" className={classes.primary}>
            <Box textAlign="center" m={1}>
              Welcome back, Please login to your account
            </Box>
          </Typography>
          <Box component="section" className={classes.formSection}>
            <ValidatorForm
              onSubmit={this.handleLogin}
              onError={errors => console.log(errors)}
              className={classes.form}
            >
              <TextValidator
                margin="normal"
                fullWidth
                label="Username"
                name="username"
                value={credentials.username}
                onChange={this.handleChange}
                validators={['required']}
                errorMessages={['* Please provide username']}
              />
              <TextValidator
                margin="normal"
                fullWidth
                label="Password"
                onChange={this.handleChange}
                name="password"
                type="password"
                validators={['required']}
                errorMessages={['* Please provide password']}
                value={credentials.password}
              />
              <Grid container className={classes.rememberSection}>
                <Grid item xs={6} md={6} lg={6} sm={6}>
                    <FormControlLabel
                      control={
                        <Checkbox value="remember" color="primary" />
                      }
                      label="Remember me"
                    />
                </Grid>
                <Grid item xs={6} md={6} lg={6} sm={6}>
                  <Box textAlign="right" m={1}>
                    <Link to="/forgot-password" component={RouterLink} variant="body2">
                      Forgot password?
                    </Link>
                  </Box>
                </Grid>
              </Grid>
              { authError }
              <Button
                type="submit"
                fullWidth
                variant="contained"
                disabled={this.props.isLoading}
                color="primary"
                className={classes.submit}
              >
                Sign in            
              </Button>
            </ValidatorForm>
          </Box>
        </Grid>
      </React.Fragment>
    )
  }
}

const mapStateToProps = state => {
  const { isLoading, authenticationError, isAuthenticated } = state.authReducer
  return {
    isAuthenticated,
    isLoading,
    authenticationError
  }
}

const mapDispatchToProps = dispatch => {
  return {
    dispatch,
    ...bindActionCreators({ 
      makeAuthRequest,
      resetAuthError 
    }, dispatch)
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Login));
