import React, { Component } from 'react'
import { withStyles } from '@material-ui/core/styles';
import WithClass from '../../hoc/withClass'
import { Route, Switch} from 'react-router-dom'
import AppBar from '../../includes/Tutor/appBar'

import { connect } from 'react-redux'
import { getTutorDashboardData, resetTutorInitialState } from '../../redux/store/actions/tutor.actions'
import { bindActionCreators } from 'redux'

import TutorDashboard from './Dashboard'
import Events from './Events/Events'
import Students from './Students/Students'

import ApiService from '../../services/api.service'

const styles = theme => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    width: '100%'
  }
});

class Home extends Component
{
  componentDidMount () {
    ApiService.setHeader()
    this.props.getTutorDashboardData()
  }

  render () {
    let url = this.props.match.url;
    return (
      <WithClass classes={this.props.classes}>
        <AppBar/>
        <main className={this.props.classes.content}>
          <div className={this.props.classes.toolbar} />
          <Switch>
            <Route exact path={`${url}/dashboard`} component={TutorDashboard}/>
            <Route path={`${url}/events`} component={Events}/>
            <Route path={`${url}/students`} component={Students}/>
          </Switch>
        </main>
      </WithClass>
    )
  }
}

const mapDispatchToProps = dispatch => {
  return {
    dispatch,
    ...bindActionCreators({ 
      getTutorDashboardData
    }, dispatch)
  }
}

export default connect(
  null,
  mapDispatchToProps
)(withStyles(styles)(Home));
