import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {withRouter} from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box'

const useStyles = makeStyles({
  root: {
    minWidth: '100%',
    boxShadow: '0px 2px 6px #00000014'
  },
});

const colorProps = {
  color: '#4D4F5C'
}

const subjectNameBoxProps = {
  border: 1,
  borderColor: '#707070',
  borderRadius: 5,
  p: 1,
  textAlign: 'center'
}

const SubjectCard = (props) => {
  const classes = useStyles()
  return (
    <Card className={classes.root}>
      <CardContent>
        <Box
          display="flex"
        >
          <Box
            component="h5"
            variant="h5"
            p={1}
            m={0}
            flexGrow={1}
            fontSize={15}
            {...colorProps}
          >
            {
              props.standard.standard_name.concat(' ', props.division.division_name)
            }
          </Box>
          <Box component="p" p={1} m={0} fontSize={11}>
            { props.total_students} Students
          </Box>
        </Box>
        <Box component="div" className={classes.subjectList}>
          {
            props.assigned_subjects.map((s) =>
              <Box component="p" 
                key={s.uuid}
                {...subjectNameBoxProps}
                {...colorProps}
              >{s.subject.subject_name}
              </Box> 
            )
          }
        </Box>
      </CardContent>
    </Card>
  );
}
export default withRouter(SubjectCard);
